  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.link {
  color: #000;
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.nav_link {
  margin-right: 20px;
